export const log = (...args) => {
  console.log('CovOne Helper:', ...args)
}

export const wait = async (ms) => {
  await new Promise((resolve) => setTimeout(resolve, ms))
}

export const setupElementTrigger = () => {
  HTMLElement.prototype.trigger = function (eStr) {
    var e = document.createEvent('HTMLEvents')
    e.initEvent(eStr, true, true)
    this.dispatchEvent(e)
  }
}

export const waitForElement = (selector, timeout = 2000, checkTimes = 10) => {
  return new Promise((resolve, reject) => {
    const duration = timeout / checkTimes
    let times = 0

    const checkElement = () => {
      const element = document.querySelector(selector)
      if (element) {
        resolve(element)
        return
      }
      if (++times >= checkTimes) {
        reject('Element does not exist.')
        return
      }

      setTimeout(checkElement, duration)
    }
    checkElement()
  })
}
