import { log, wait, setupElementTrigger, waitForElement } from '../_utility'

export default () => {
  const setup = (parent) => {
    setupElementTrigger()

    const div = document.createElement('div')
    const h3 = document.createElement('h3')
    h3.innerText = 'Bok Helper'

    const button = document.createElement('button')
    button.innerText = 'クリップボードからコマンドを反映'
    button.setAttribute('id', 'apply-command')
    button.addEventListener('click', async (event) => {
      const value = await navigator.clipboard.readText()
      if (!value) return

      try {
        const texts = value.split(':')
        const attrs = texts[1].split(',')
        const values = texts[0].split(',')

        for (const [index, attr] of attrs.entries()) {
          let value = values[index]
          if (!value) continue
          console.log(attr, value)

          const targetInput = document.querySelector(
            `.BoKpdf_prop_value_${attr}`,
          )
          console.log(targetInput)
          if (targetInput) {
            if (attr === 'fieldcode' && value === '現在日時') {
              value = '&DATE'
            }
            if (
              attr === 'fieldcode' &&
              !targetInput.querySelector(`option[value="${value}"]`)
            ) {
              continue
            }

            if (value.match(/^\!.+/)) continue

            targetInput.value = value

            targetInput.focus()
            if (targetInput.nodeName === 'SELECT') {
              targetInput.trigger('change')
            } else {
              targetInput.trigger('change')
              targetInput.trigger('keyup')
            }
          } else {
            const targetRadio = document.querySelector(
              `[name=BoKpdf_prop_value_${attr}][value=${value}]`,
            )
            if (!targetRadio) continue
            targetRadio.checked = true
            targetRadio.focus()
            targetRadio.trigger('change')
          }
        }
      } catch (e) {
        alert('コマンドの実行に失敗しました\n' + e.message)
        return
      }
    })

    const blockquote = document.createElement('blockquote')
    blockquote.innerText = `
複製: Ctrl + d
削除: Ctrl + Backspace
ツールバー選択: Ctrl + 1-6
コマンド適用: Ctrl + a
`

    div.appendChild(h3)
    div.appendChild(button)
    div.appendChild(blockquote)
    parent.appendChild(div)

    document.addEventListener('keydown', async (event) => {
      // log(event)
      if (event.ctrlKey && event.key === 'd') {
        log('Shortcut', 'Dupulicate')
        document
          .querySelector('.reportpdf_report_config_dialog_itemCopy')
          .trigger('click')
        await wait(100)
        document
          .querySelector('.reportpdf_report_config_dialog_itemPaste')
          .trigger('click')
        return false
      } else if (event.ctrlKey && event.key === 'Backspace') {
        log('Shortcut', 'Delete')
        document
          .querySelector('.reportpdf_report_config_dialog_itemRemove')
          .trigger('click')
        return false
      } else if (event.ctrlKey && event.key === 'a') {
        log('Shortcut', 'Apply Command')
        document.querySelector('#apply-command').trigger('click')
        return false
      } else if (event.ctrlKey && event.key.match(/[1-6]/)) {
        log('Shortcut', 'Toolbox', event.key)
        document
          .querySelector(`.reportpdf_report_toolbox:nth-child(${event.key})`)
          .trigger('click')
        return false
      }
    })
  }

  const main = async () => {
    try {
      const bokLayoutButton = await waitForElement(
        '#kintoneplugin-pattern-tbody > tr:nth-child(2) > td:nth-child(5) > div > div > div > button.template_setting',
        3000,
      )

      bokLayoutButton.addEventListener(
        'click',
        async () => {
          const parent = await waitForElement(
            '#ui-id-1 > table > tbody > tr > td:nth-child(3) > table > tbody > tr:nth-child(2) > td',
            2000,
          )
          setup(parent)
        },
        { once: true },
      )
      log('Bok installed')
    } catch (e) {
      log('Bok not installed', e)
    }
  }
  main()
}
