import { getAppsInfo, getAppFields } from '../_kintone'
import pMap from 'p-map'

// window.app.getAppsId()
const getAppsId = async () => {
  const apps = await getAppsInfo()
  console.log(apps)

  const filterApps = []
  for (let i = 1; i <= 42; i++) {
    const app = apps.find(
      (app) => !!app.name.match(new RegExp(`^CovOne_F${i}[^0-9].+$`)),
    )
    filterApps.push({
      id: app ? app.appId : -1,
      name: `F${i}`,
    })
  }

  console.log(filterApps)

  let text = ''
  filterApps.forEach((app) => {
    text += `${app.id != -1 ? app.id : ''}\t${app.name}\n`
  })
  console.log(text)

  let envText = ''
  filterApps.forEach((app) => {
    if (app.id != -1) envText += `${app.id},`
  })
  console.log(envText)
}

const getRelatedFieldApps = async (fieldCode) => {
  const apps = await getAppsInfo()
  console.log(apps)

  await pMap(apps, async (app) => {
    const fields = await getAppFields(app.appId)

    const key = Object.keys(fields).find((key) => key.match(new RegExp(`^${fieldCode}$`)))
    if(!key) return
    console.log('app', app.appId, app.name, fields[key])
  })
}

export default () => {
  window.app = {
    getAppsId,
    getRelatedFieldApps
  }
}
