const bokDataLoop = (data, newItemCreator) => {
  data.page.map((page, pageIndex) => {
    page.output.map((output, outputIndex) => {
      const outputString = JSON.stringify(output)
      const newItem = newItemCreator(output, outputIndex)
      if (!newItem) return
      console.log(pageIndex, outputIndex, outputString, JSON.stringify(newItem))
      data.page[pageIndex].output[outputIndex] = newItem
    })
  })
  return data
}

const bokDataNum = (input, codes) => {
  // window.bokData.setNumberType(datadata, ['HER_SYS_ID', '県ナンバー', '市ナンバー', '年齢', '月齢', '喫煙開始年齢', '喫煙終了年齢', '喫煙一日本数', '妊娠週数', '妊娠月数', 'BMI', '身長', '体重', '高齢障害者身体手帳種', '高齢障害者身体手帳等級', '高齢障害者精神手帳等級', '接触者例目', '濃厚接触者年齢'])
  const output = bokDataLoop(JSON.parse(input), (item, itemIndex) => {
    if (!codes.find((value) => value === item.fieldCode)) return null
    item.type = 'NUM'
    return item
  })
  console.log(JSON.stringify(output))
}

const bokDataFontSize = (input, fontSize) => {
  // window.bokData.setNumberType(datadata, ['HER_SYS_ID', '県ナンバー', '市ナンバー', '年齢', '月齢', '喫煙開始年齢', '喫煙終了年齢', '喫煙一日本数', '妊娠週数', '妊娠月数', 'BMI', '身長', '体重', '高齢障害者身体手帳種', '高齢障害者身体手帳等級', '高齢障害者精神手帳等級', '接触者例目', '濃厚接触者年齢'])
  const output = bokDataLoop(JSON.parse(input), (item, itemIndex) => {
    // if (!codes.find((value) => value === item.fieldCode)) return null
    if (item.mode !== 'item_char') return
    item.fontSize = fontSize
    return item
  })
  console.log(JSON.stringify(output))
}

export default () => {
  window.bokData = {
    bokDataNum,
    bokDataFontSize,
  }
}
