export const getAppInfo = async (app) => {
  return await kintone.api(kintone.api.url('/k/v1/app', true), 'GET', {
    id: app,
  })
}

export const getAppsInfo = async () => {
  return (await kintone.api(kintone.api.url('/k/v1/apps', true), 'GET', {})).apps
}

export const getAppFields = async (app) => {
  return (
    await kintone.api(kintone.api.url('/k/v1/app/form/fields', true), 'GET', {
      app,
    })
  ).properties
}

export const getAppRecord = async (app, query) => {
  const records = (
    await kintone.api(kintone.api.url('/k/v1/records', true), 'GET', {
      app,
      query,
    })
  ).records
  return records.length ? records[0] : null
}

export const createAppRecord = async (app, field) => {
  return (
    await kintone.api(kintone.api.url('/k/v1/records', true), 'POST', {
      app,
      records: [field], // { HER_SYS_ID: {value: 1} }
    })
  ).ids[0]
}

export const updateAppRecord = async (app, updateKey, fields) => {
  await kintone.api(kintone.api.url('/k/v1/record', true), 'PUT', {
    app,
    updateKey, // { field: 'HER_SYS_ID', value: 1, },
    record: fields,
  })
}
